/* Обнуление */
*,
*::before,
*::after {
	padding: 0;
	margin: 0;
	border: 0;
	box-sizing: border-box;
}
a {
	text-decoration: none;
}
ul,
ol,
li {
	list-style: none;
}
img {
	vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: inherit;
	font-size: inherit;
}
html,
body {
	height: 100%;
	line-height: 1;

  background-color: rgb(166, 221, 15);
	font-family: Rubik;
    font-size: 20px;
    color: #000000;
}
/* ---------------------------------------------------------------------------- */
.wrapper {
	overflow: hidden;
    display: flex;
    flex-direction: column;
    min-height: 100%;


	


	display: grid;
	grid-template-rows: 60px 1fr;
	grid-template-columns: 2fr 10fr;
	grid-template-areas:
	"h h"
	"f m";
	grid-gap:10px;
}

.header{
	grid-area: h;
	
}	

.main{
	grid-area: m;
}

.footer{
	grid-area: f;
}


/* ---------------------------------------------------------------------------- */
.main{
    flex: 1 1 auto;
}

.container{
    max-width: 1087px;
    margin: 0 auto;
    padding: 0 15px;
}
